.mkdf-side-menu {
    // widgets spacing
    $widget-bottom-margin: 51px;

    // list items dimensions
    $widget-list-item-height: 24px;
    $widget-list-item-padding: 0;

    // list items color
    $widget-list-item-background-color: #999999;

    // list items border
    $widget-list-item-border-color: #ffffff;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // title
    $widget-title-margin: 18px;

    // text
    $widget-text-color: #ffffff;
    $widget-heading-color: #ffffff;

    // links
    $widget-link-color: #ffffff;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: 43px;
    $widget-tag-cloud-height: 20px;

    .widget {
        font-size: 14px;

        &.widget_archive,
        &.widget_categories,
        &.widget_pages,
        &.widget_meta,
        &.widget_nav_menu,
        &.widget_recent_entries {
            text-transform: uppercase;
        }

        border-bottom: 1px solid #ffffff;
        padding-bottom: $widget-bottom-margin !important;

        &:nth-last-child(3), // two hidden sidearea divs at bottom
        &.mkdf-separator-widget {
            border-bottom: 0;
            padding-bottom: 0 !important;
        }
    }

    @import 'default-wp-widgets/default-wordpress-widgets';
}