/* ==========================================================================
   Social Icons Group widget style - begin
   ========================================================================== */

.mkdf-social-icons-group-widget {
    &.mkdf-square-icons {
        .mkdf-social-icon-widget-holder {
            position: relative;
            width: 44px;
            height: 44px;
            margin: 0 7px;
            font-size: 17px;

            .mkdf-social-icon-widget {
                position: absolute;
                top: 50%;
                left: 50%;

                @include mkdfTransform(translateY(-50%) translateX(-50%));
            }
        }
    }

    .mkdf-social-icon-widget-holder {
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        text-align: center;

        &:after {
            content: '|';
            padding: 0 12px;
        }

        &:first-child {
            margin-left: 0;
        }

        &:last-child {
            margin-right: 0;

            &:after {
                display: none;
            }
        }
    }
}

/* ==========================================================================
   Social Icons Group widget style - end
   ========================================================================== */