.mkdf-menu-area {
    .mkdf-header-cart-svg-path {
        top: -1px;
    }

    .mkdf-search-opener-svg-path {
        top: 0;
    }

    .mkdf-side-menu-button-opener-svg-path {
        top: -2px;
    }

    .mkdf-light-header & {
        .mkdf-separator {
            border-color: #ffffff !important;
        }
    }

    .mkdf-dark-header & {
        .mkdf-separator {
            border-color: #000000 !important;
        }
    }
}

