aside.mkdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    @include ipad-landscape {
        margin: 73px 0 64px;
    }
}

.wpb_widgetised_column {
    position: relative;
}

aside.mkdf-sidebar,
.wpb_widgetised_column {
    // widgets spacing
    $widget-bottom-margin: 35px;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 9px;

    // list items color
    $widget-list-item-background-color: #999999;

    // list items border
    $widget-list-item-border-color: $default-text-color;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // title
    $widget-title-margin: 4px;

    // text
    $widget-text-color: $default-text-color;
    $widget-heading-color: $default-text-color;

    // links
    $widget-link-color: $default-text-color;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: 43px;
    $widget-tag-cloud-height: 20px;

    .widget {
        font-size: inherit;

        &.widget_archive,
        &.widget_categories,
        &.widget_pages,
        &.widget_meta,
        &.widget_nav_menu,
        &.widget_recent_entries {
            @include mkdfH5();
        }
    }

    @import 'default-wp-widgets/default-wordpress-widgets';
}