/* ==========================================================================
   Pretty Photo style - begin
   ========================================================================== */

body {

    .pp_pic_holder {
        display: none;
        left: 50% !important;
        position: fixed !important;
        top: 50% !important;
        width: 100px;
        z-index: 10001 !important;

        @include mkdfTransform(translateX(-50%) translateY(-50%));

        @include ipad-portrait {
            width: 90% !important;
        }

        &.is-single {
            .pp_details {
                display: none;
            }
        }

        .pp_top,
        .pp_bottom {
            display: none !important;
        }

        .pp_content {
            background: none;
            height: 40px;
            margin: 0 auto;
            min-width: 40px;
            position: relative;
            width: 100%;

            @include ipad-portrait {
                width: 100% !important;
            }

            * html & {
                width: 40px;
            }
        }

        .pp_content_container {
            position: relative;
            text-align: left;
            width: 100%;

            .pp_left,
            .pp_right {
                background: none !important;
                padding: 0 !important;
            }
        }

        #pp_full_res {
            line-height: 1 !important;

            .pp_inline {
                color: $default-text-color;
                text-align: left;

                p {
                    margin: 0 0 15px;
                }
            }

            iframe {
                width: 100% !important;
            }

            img {
                display: block;

                @include ipad-portrait {
                    height: auto !important;
                    width: 100% !important;
                }
            }
        }

        .ppt,
        .pp_gallery,
        .pp_description {
            display: none !important;
        }

        .pp_loaderIcon {
            animation: scaleout 1.0s infinite ease-in-out;
            -webkit-animation: scaleout 1.0s infinite ease-in-out;
            background-color: #fff;
            border-radius: 16px;
            height: 32px;
            left: 50%;
            margin: -16px 0 0 -16px;
            position: absolute;
            top: 50%;
            width: 32px;
        }

        .pp_fade {
            display: none;
            position: relative;
            top: 50%;

            @include mkdfTransform(translateY(-50%) translateZ(0));
        }

        .pp_details {
            background-color: #fff;
            box-sizing: border-box;
            display: table;
            float: none !important;
            margin: 0 !important;
            padding: 15px 24px;
            position: relative;
            top: -1px;
            vertical-align: top;
            width: 100% !important;

            .pp_nav {
                display: table-cell;
                float: none;
                height: auto;
                margin: 0 !important;
                position: relative;
                vertical-align: middle;
                width: 100%;

                .pp_play,
                .pp_pause {
                    display: none;
                }

                .currentTextHolder {
                    color: $default-text-color;
                    float: none;
                    font-family: inherit;
                    font-size: inherit;
                    font-style: inherit;
                    left: 50%;
                    letter-spacing: 5px;
                    line-height: 30px;
                    margin: 0;
                    padding: 0;
                    position: absolute;
                    top: 0;
                    width: auto;
                }
            }
        }

        a {
            &:focus {
                outline: none;
            }

            &.pp_arrow_previous,
            &.pp_arrow_next {
                background: none !important;
                color: $default-text-color;
                display: inline-block;
                float: none;
                font-family: 'FontAwesome';
                font-size: 0;
                height: auto !important;
                left: auto !important;
                line-height: 1;
                margin: 0 !important;
                opacity: 1 !important;
                overflow: visible;
                position: relative;
                text-indent: 0;
                vertical-align: top;
                width: auto !important;

                &:after {
                    font-size: 30px;
                }

                &:hover {
                    color: $default-text-color;
                }
            }

            &.pp_arrow_previous {
                &:after {
                    content: "\f053";
                }
            }

            &.pp_arrow_next {
                margin: 0 0 0 14px !important;

                &:after {
                    content: "\f054";
                }
            }

            &.pp_next,
            &.pp_previous {
                background: none !important;
                color: $default-text-color;
                display: inline-block;
                font-family: 'FontAwesome';
                font-size: 30px;
                height: auto;
                line-height: 1;
                margin: 0 !important;
                opacity: 0;
                position: absolute;
                text-indent: -9999px !important;
                top: calc(50% - 20px);
                width: auto;

                @include mkdfTransition(opacity 0.2s ease-out, color 0.2s ease-out);

                > * {
                    display: none;
                }

                &:after {
                    display: block;
                    line-height: inherit;
                    text-indent: 0 !important;
                }

                &:hover {
                    color: rgba($default-text-color, .8);
                }
            }

            &.pp_previous {
                left: 30px;

                @include ipad-portrait {
                    left: 20px;
                }

                &:after {
                    content: "\f053";
                }
            }

            &.pp_next {
                right: 30px;

                @include ipad-portrait {
                    right: 20px;
                }

                &:after {
                    content: "\f054";
                }
            }

            &.pp_close {
                background: none !important;
                color: $default-text-color;
                cursor: pointer;
                display: table-cell;
                font-family: 'FontAwesome';
                font-size: 0;
                height: auto;
                line-height: 1;
                margin: 0 !important;
                opacity: 1 !important;
                position: relative;
                text-indent: 0;
                vertical-align: middle;
                width: auto;

                &:after {
                    content: "\f00d";
                    font-size: 30px;
                }

                &:hover {
                    color: $default-text-color;
                }
            }

            &.pp_expand {
                background: none !important;
                color: #fff;
                display: block;
                font-family: 'FontAwesome';
                font-size: 0;
                height: auto;
                line-height: 1;
                position: absolute;
                right: -22px;
                text-align: center;
                text-indent: 0;
                top: -18px;
                width: auto;
                z-index: 2001;

                &:after {
                    content: "\f065";
                    font-size: 22px;
                }

                &:hover {
                    color: rgba(#fff, .8);
                }
            }
        }
    }

    .pp_hoverContainer {
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 2000;

        @include ipad-portrait {
            height: 100% !important;
            width: 100% !important;
        }

        &:hover {
            a {
                &.pp_next,
                &.pp_previous {
                    opacity: 1;
                }
            }
        }
    }

    .pp_overlay {
        background-color: $default-text-color;
        display: none;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 10000 !important;
    }
}

/* ==========================================================================
   Pretty Photo style - end
   ========================================================================== */