@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();

    box-sizing: border-box;
    margin: 0;
    padding: 0;

    .mkdf-comment-holder-inner {
        border-top: 1px solid $default-text-color;
        margin-top: 80px;
        padding-top: 69px;
    }

    .mkdf-comment {
        &.mkdf-pingback-comment {
            .mkdf-comment-text {
                padding: 0;
            }
        }
    }

    .mkdf-comment-list {
        list-style: none;
        margin: 0;
        padding: 0;
        position: relative;
        z-index: 150;

        li {
            @include mkdfRelativeHolderLayout();

            margin: 12px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            border: 0;
            margin: 0;
            padding: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 32px;

        > * {
            margin: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // single comment

    .mkdf-comment-image {
        display: block;
        float: left;
        height: 82px;
        left: 0;
        position: relative;
        top: 6px;
        width: 82px;

        img {
            display: block;
        }
    }

    .mkdf-comment-text {
        min-height: 82px;
        padding: 0 0 0 121px;

        .mkdf-comment-info {
        }

        .mkdf-comment-name {
            @include mkdfRelativeHolderLayout();

            padding-right: 20px;
            vertical-align: baseline;
            width: auto;
        }

        .mkdf-comment-date,
        .replay,
        .comment-reply-link,
        .comment-edit-link {
            @include mkdfRelativeHolderLayout();
            @include mkdfH5();

            vertical-align: middle;
            width: auto;

            &:after {
                content: '|';
                padding: 0 16px;
            }

            &:last-child:after {
                display: none;
            }

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-comment-date {
            &:after {
                padding: 0 16px 0 20px;
            }

            &:hover {
                color: currentColor;
            }
        }

        .mkdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            .comment-reply-title {
                display: block;

                small {
                    @include mkdfH5();

                    color: $first-main-color;
                    padding-left: 20px;

                    &:hover {
                        color: $default-text-color;
                    }
                }
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        ul, ol {
            li {
                display: list-item;
            }
        }

        li {
            border: 0 !important;
            margin: 0 !important;
            padding: 0 !important;
        }
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// comment form

.mkdf-comment-form {
    box-sizing: border-box;
    padding-bottom: 37px;
    padding-top: 60px;

    .mkdf-comment-form-inner {
    }

    .comment-reply-title {
        margin: 0 0 36px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {
    .mkdf-comment-holder {
        .mkdf-comment-list {
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
    .mkdf-comment-holder {
        .mkdf-comment-list {
            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {
            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

            .reply,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
    .mkdf-comment-holder {
        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            min-height: inherit;
            padding: 20px 0 0;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;

    cursor: text;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonMediumParams();
    @include mkdfButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include mkdfButtonSolidHoverColor();
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
    height: 150px !important;
    padding: 0 $input-padding;
}

.wpcf7 {
    .ajax-loader {
        display: none !important;
    }

    @media (min-width: $ipad-portrait) {
        .mkdf-grid-col-8 {
            float: left;
            width: 66.66667%;
        }

        .mkdf-grid-col-4 {
            float: left;
            width: 33.33333%;
        }
    }
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

input:not([type="checkbox"]),
textarea {
    -webkit-appearance: none;
    -webkit-border-radius: 0;
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
    $arrow: 30px;

    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                margin-top: - #{($arrow / 2) + 23px};
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {
        .owl-prev,
        .owl-next {
            color: $default-text-color;
            margin-top: -#{$arrow / 2};
            position: absolute;
            top: 50%;
            z-index: 3;

            @include mkdfTransition(color .2s ease-out);

            &:hover {
                color: $first-main-color;
            }

            > span {
                background-color: currentColor;
                color: inherit;
                display: block;
                height: 30px;
                position: relative;
                width: 30px;

                @include mkdfMaskImage('../img/arrow.svg');
            }
        }

        .owl-prev {
            left: 30px;

            @include mkdfTransform(scaleX(-1));
        }

        .owl-next {
            right: 30px;
        }
    }

    .owl-dots {
        margin: 16px 0 0;
        text-align: center;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;

            span {
                background-color: #c9c9c9;
                border-radius: 3em;
                box-sizing: border-box;
                display: inline-block;
                height: 10px;
                margin: 0 6px;
                vertical-align: middle;
                width: 10px;

                @include mkdfTransition(background-color .2s ease-out);
            }

            &:hover,
            &.active {
                span {
                    background-color: #5e5e5e;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        list-style: none;
        margin: 20px -10px 0;
        vertical-align: middle;

        > .mkdf-slider-thumbnail-item {
            box-sizing: border-box;
            cursor: pointer;
            float: left;
            line-height: 0;
            margin-bottom: 20px;
            padding: 0 10px;

            img {
                border: 2px solid transparent;

                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {
                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .mkdf-wrapper {
        padding: 0 !important;
    }

    .mkdf-content {
        height: 100vh;
    }

    &.admin-bar {
        .mkdf-content {
            height: calc(100vh - 32px);
        }
    }

    .mkdf-content-inner {
        @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
        @include mkdfTableCellLayout();

        padding: 0 35%;
        text-align: center;

        .mkdf-404-title {
            margin: 39px 0 0;
        }

        .mkdf-404-subtitle {
            margin: 35px 0 0;
        }

        .mkdf-404-text {
            margin: 10px 0 0;
        }

        .mkdf-btn {
            margin: 40px 0 0;

            &.mkdf-btn-light-style {
                border-color: #ffffff;
                color: #ffffff;

                &:hover {
                    background-color: #ffffff !important;
                    border-color: #ffffff !important;
                    color: $default-text-color !important;

                    > span {
                        color: $default-text-color !important;
                    }
                }
            }
        }
    }

    @include ipad-landscape {
        .mkdf-content {
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {
            .mkdf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }

        .mkdf-page-not-found {
            padding: 30% 5% 0%;
        }
    }

    @include ipad-portrait {
        .mkdf-page-not-found {
            padding: 25% 20% 0;
        }
    }

    @include phone-landscape {
        .mkdf-page-not-found {
            padding: 45% 5% 0%;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    $size: 44px;
    $offset: 11px;

    background-color: transparent;
    border: 2px solid $first-main-color;
    border-radius: 50%;
    bottom: 25px;
    box-sizing: content-box;
    color: $first-main-color;
    display: block;
    height: $size;
    margin: 0;
    opacity: 0;
    overflow: hidden;
    position: fixed;
    right: 25px;
    visibility: hidden;
    width: $size;
    z-index: 10000;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include mkdfTransition(all 0.3s ease);
    }

    .mkdf-arrow {
        background-color: transparent;
        background-position: center;
        background-size: cover;
        color: inherit;
        display: block;
        height: 13px;
        left: 15px;
        position: absolute;
        top: 18px;
        width: 13px;
        z-index: 5;
        border-top: 2px solid currentColor;
        border-right: 2px solid currentColor;
        @include mkdfTransition(all 0.2s ease);
        @include mkdfTransform(rotate(-45deg));
    }

    &:hover {
        background: $first-main-color;

        .mkdf-arrow {
            border-top: 2px solid #fff;
            border-right: 2px solid #fff;
        }
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */

.mkdf-self-hosted-video-holder {
    .mkdf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

