/* ==========================================================================
   Instagram widget style - begin
   ========================================================================== */

.mkdf-instagram-feed {
    list-style: none;
    margin: 0;
    padding: 0;

    li {
        border: none !important;
        box-sizing: border-box;
        float: left;
        padding: 0 !important;

        a {
            display: block;
            overflow: hidden;
            position: relative;

            &:after {
                background-color: rgba($default-text-color, .4);
                content: '';
                height: 100%;
                left: 0;
                opacity: 0;
                position: absolute;
                top: 0;
                width: 100%;

                @include mkdfTransition(opacity .6s);
            }

            .mkdf-instagram-icon {
                color: #ffffff !important;
                font-size: 40px;
                left: 50%;
                opacity: 0;
                position: absolute;
                top: 50%;
                z-index: 999;

                @include mkdfTransition(opacity .6s);
                @include mkdfTransform(translate(-50%, -50%));
            }

            &:hover {
                .mkdf-instagram-icon,
                &:after {
                    opacity: 1;
                }
            }
        }

        img {
            display: block;
            width: 100%;
        }
    }

    &.mkdf-instagram-gallery {

        $gallery_space_label: ('no', 'tiny', 'small', 'normal');
        $gallery_space_width: (0, 5, 10, 15);

        @for $i from 0 to length($gallery_space_label) {
            &.mkdf-#{nth($gallery_space_label,$i+1)}-space {
                $column_width: nth($gallery_space_width, $i+1);
                $column_width_margin: $column_width * 2;

                @if ($column_width == 0) {
                    margin: 0;
                } @else {
                    margin: 0 -#{$column_width}px -#{$column_width_margin}px;
                }

                li {
                    padding: 0 #{$column_width}px;
                    margin: 0 0 #{$column_width_margin}px;
                }
            }
        }

        $gallery_columns: ('2', '3', '4', '6', '9');
        @for $i from 0 to length($gallery_columns) {
            &.mkdf-col-#{nth($gallery_columns, $i+1)} {
                $column_width: 100%/($i+2);

                @if ($i == 3) {
                    $column_width: 100%/6;
                } @else if ($i == 4) {
                    $column_width: 100%/9;
                }

                li {
                    width: $column_width;
                }
            }
        }
    }

    &.mkdf-instagram-carousel {
        li {
            margin: 0;
            padding: 0;
            position: relative;
            width: 100%;

            a {
                display: block;
                height: 100%;
                position: relative;

                &:before {
                    background-color: rgba(#fff, .25);
                    content: '';
                    height: 100%;
                    left: 0;
                    opacity: 0;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    @include mkdfTransition(opacity 0.3s ease-in-out);
                }

                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}

/* ==========================================================================
   Instagram widget style - end
   ========================================================================== */